
import qs from 'qs';
import InlineXSwitch from 'vux/src/components/inline-x-switch/index.vue'

export default {
components: {
InlineXSwitch
},
data() {
return {
linkmanName: "",
phone: "",
email: "",
isDef:0,
isDefault:false,
};
},
methods: {
checkPlayerPhone() {},
saveDate() {
if(this.isDefault){
this.isDef = 0;
}else{
this.isDef = 1;
}
let params ={
linkman:this.linkmanName,
phone:this.phone,
email:this.email,
isDef:this.isDef,
}
this.$axios.post('/api/linkman/add',qs.stringify(params)).then( res =>{
if(res.data.code == 0){
this.$router.push({name:'commonInformation'})
}
})
},
cancleBtn() {
this.$router.push({name:'commonInformation'})
}
}
};



